import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './types'

Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        token: null,
        isLogin: false,
    },
    mutations:{
        [types.Login]:(token) => {
            this.token = token;
            this.isLogin = true;
        }
    },
    modules: {
    },

})


